/* eslint-disable max-len */
<template>
  <div class="container home">
    <div class="row justify-content-center weather">
      <div class="col-12 col-sm-9 col-lg-6 offset-3 m-auto header">
        <div class="input-group mb-3 mt-2">
          <!-- eslint-disable-next-line -->
          <input
            v-model="location"
            v-on:keyup.enter="getLocation"
            type="text"
            class="form-control"
            placeholder="Enter Location"
          />
          <div class="input-group-append">
            <!-- eslint-disable-next-line -->
            <button
              v-on:click="getLocation"
              class="btn btn-outline-secondary"
              type="button"
            >Search</button>
          </div>
        </div>
      </div>
      <!-- eslint-disable-next-line -->
      <div class="col-12 col-sm-9 col-lg-8 offset-3 m-auto text-center main  align-self-center" v-if="forecast">
        <div class="card border-primary mb-3">
          <!-- eslint-disable-next-line -->
          <div class="card-header">
            <!-- <span>{{ place }}</span> -->
            <span>{{ place.name }}</span>
            <span class="icon">{{icons[forecast.currently.icon]}}</span>
          </div>
          <div class="card-body">
            <!-- eslint-disable-next-line -->
            <div class="card-text temp">
              <!-- eslint-disable-next-line -->
              <p v-on:click="metric = !metric" class="temp-val">{{ metric === true ? toCelsius(forecast.currently.temperature) + '‚ÑÉ' : forecast.currently.temperature.toFixed(1) + '‚Ñâ' }}</p>
              <p>{{forecast.currently.summary}}</p>
            </div>
            <div class="card-text details">
              <div class="row">
                <p>
                  Precipitation
                  <span>‚òîÔ∏è {{forecast.currently.precipProbability}}%</span>
                </p>
                <p>
                  Wind
                  <!-- eslint-disable-next-line -->
                  <span>üå¨ {{ metric === true ? convertWind.toFixed(1) + ' km/h' : forecast.currently.windSpeed.toFixed(1) + ' mph' }}</span>
                </p>
                <p>
                  Humidity
                  <span>üíß {{ forecast.currently.humidity | toOneDecimal }}%</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="col-8 offset-2 text-center footer"></div> -->
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue';

import API from '@/lib/API';

export default {
  name: 'home',
  data() {
    return {
      metric: true,
      coords: {},
      location: localStorage.location || null, // '',
      place: localStorage.place || {}, // '',
      forecast: null,
      icons: {
        'clear-day': '‚òÄÔ∏è',
        'clear-night': 'üåô',
        rain: '‚õà',
        snow: '‚ùÑÔ∏è',
        sleet: 'üå®',
        wind: 'üå¨',
        fog: 'üå´',
        cloudy: '‚òÅÔ∏è',
        'partly-cloudy-day': '‚õÖÔ∏è',
        'partly-cloudy-night': '‚òÅÔ∏è',
      },
    };
  },
  // created() {
  //   this.geolocation();
  // },
  mounted() {
    this.geolocation();
  },
  /* eslint-disable */
  methods: {
    loadWeather(lat, long) {
      localStorage.lat = this.lat;
      localStorage.long = this.long;
      API.getPlace(lat, long).then(result => {
        this.place = result;
        // this.place = [result.name, result.street].join(' ');
        localStorage.place = this.place;
      });
      API.getWeather(lat, long).then(result => {
        this.forecast = result;
      });
    },
    geolocation() {
      navigator.geolocation.getCurrentPosition(this.buildUrl);
    },
    buildUrl(position) {
      const lat = position.coords.latitude;
      const long = position.coords.longitude;
      API.getPlace(lat, long).then(result => {
        this.place = result;
        localStorage.place = this.place;
      });
      API.getWeather(lat, long).then(result => {
        this.forecast = result;
      });
    },
    getLocation() {
      localStorage.location = this.location;
      API.getCoords(this.location).then(result => {
        this.loadWeather(result.latitude, result.longitude);
      });
      this.location = "";
    },
    toCelsius(value) {
      let celsius = Math.round((5 / 9) * (value - 32) * 10) / 10;
      return celsius;
    },
    toOneDecimal(value) {
      return Math.round(value * 10) / 10;
    }
  },
  computed: {
    convertWind() {
      return this.forecast.currently.windSpeed * 1.6;
    }
  },
  filters: {
    toOneDecimal(value) {
      return Math.round(value * 10) / 10;
    }
  }
};
</script>
