const proxy = 'https://cors-anywhere.herokuapp.com/';
// const API_KEY = '63015029d55e3f9ad26acc73f069f6f7';
const API_KEY = process.env.VUE_APP_API_KEY;

const API_URL = `${proxy}https://api.darksky.net/forecast/${API_KEY}/`;
// const API_URL = `${proxy}https://api.darksky.net/forecast/${process.env.VUE_APP_API_KEY}/`;

const GEO_API = `${proxy}https://darksky.net/geo?q=`;
const PLACE_API = `${proxy}https://darksky.net/rgeo?hires=1`;

function getCoords(location) {
  return fetch(`${GEO_API}${location}`)
    .then(response => response.json());
}

function getWeather(lat, long) {
  return fetch(`${API_URL}${lat},${long}`)
    .then(response => response.json());
}

function getPlace(lat, long) {
  return fetch(`${PLACE_API}&lat=${lat}&lon=${long}`)
    .then(response => response.json());
}

export default { getWeather, getCoords, getPlace };
